
Template.body.addContent((function() {
  var view = this;
  return HTML.DIV({
    class: "row"
  }, "\n\t\t", Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n\t\t    ", Blaze.If(function() {
      return Spacebars.call(view.lookup("currentUser"));
    }, function() {
      return [ "\n\t\t    \t", Spacebars.include(view.lookupTemplate("navBarTemplate")), "\n\t\t  \t" ];
    }), "\n\t  \t" ];
  }, function() {
    return [ "\n\t\t    ", Spacebars.include(view.lookupTemplate("loading")), "\n\t  \t" ];
  }), HTML.Raw("\n\t\t<!--{{> btnFloating}}-->\n\t"));
}));
Meteor.startup(Template.body.renderToDocument);
Meteor.startup(function() {
  var attrs = {"id":"bodyPpal"};
  for (var prop in attrs) {
    document.body.setAttribute(prop, attrs[prop]);
  }
});
