
Template.__checkName("twiitPageNew");
Template["twiitPageNew"] = new Template("Template.twiitPageNew", (function() {
  var view = this;
  return [ HTML.Raw('<!--\n  <nav class="navbar navbar-default">\n    <div class="container">\n      Brand and toggle get grouped for better mobile display\n      <div class="navbar-header">\n\n        <button type="button" id="btnNewTweet" class="navbar-toggle">\n          <span class="glyphicon glyphicon-heart"></span>\n        </button>\n\n        <button type="button" id="btnNewTweet" class="navbar-toggle">\n          <span class="glyphicon glyphicon-comment"></span>\n        </button>\n\n        <button type="button" id="btnNewTweet" class="navbar-toggle">\n          <span class="glyphicon glyphicon-pencil"></span>\n        </button>\n\n      </div>\n    </div>\n  </nav>\n  -->\n  '), Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n    ", HTML.DIV({
      class: "tweetfeed-container"
    }, "\n      ", HTML.DIV({
      class: "panel panel-default tweetfeed"
    }, "\n        ", HTML.DIV({
      class: "panel-body"
    }, "\n          ", HTML.Comment(" Texto para el contenido del Twitt "), "\n          ", Blaze.If(function() {
      return Spacebars.call(view.lookup("countNotifTwiit"));
    }, function() {
      return [ "\n            ", Blaze.Each(function() {
        return Spacebars.call(view.lookup("tweetMessage"));
      }, function() {
        return [ "\n              ", Blaze.View("lookup:saveIdInArr", function() {
          return Spacebars.mustache(view.lookup("saveIdInArr"));
        }), "\n              ", HTML.DIV({
          class: "panel panel-info"
        }, "\n                ", HTML.DIV({
          class: "panel-heading"
        }, "\n                  ", HTML.H3({
          class: "panel-title"
        }, "\n                    ", HTML.A({
          href: function() {
            return Spacebars.mustache(view.lookup("pathFor"), "userProfile", Spacebars.kw({
              username: Spacebars.dot(view.lookup("."), "actorNotif")
            }));
          }
        }, "@", Blaze.View("lookup:..actorNotif", function() {
          return Spacebars.mustache(Spacebars.dot(view.lookup("."), "actorNotif"));
        })), " \n                    ", HTML.SPAN({
          class: "glyphicon glyphicon-triangle-right",
          "aria-hidden": "true"
        }), "\n                     ", Blaze.View("lookup:convertDateTime", function() {
          return Spacebars.mustache(view.lookup("convertDateTime"));
        }), "\n                   "), "\n                "), "\n                ", HTML.DIV({
          class: "panel-body"
        }, "\n                  ", Blaze.View("lookup:..twiitMessage", function() {
          return Spacebars.mustache(Spacebars.dot(view.lookup("."), "twiitMessage"));
        }), "\n                "), "\n                ", HTML.DIV({
          class: "btn-group",
          role: "group",
          id: "btnGroupInteractions"
        }, "\n                  ", HTML.A({
          class: "btn btn-secondary"
        }, "\n                    ", HTML.SPAN({
          class: "glyphicon glyphicon-bullhorn"
        }), "\n                  "), "\n                  ", Blaze.If(function() {
          return Spacebars.call(view.lookup("findNumComment"));
        }, function() {
          return [ "\n                    ", HTML.A({
            href: function() {
              return Spacebars.mustache(view.lookup("pathFor"), "twiitCommentPage", Spacebars.kw({
                _id: Spacebars.dot(view.lookup("."), "_id")
              }));
            },
            class: "btn btn-secondary"
          }, "\n                      ", HTML.SPAN({
            class: "glyphicon glyphicon-comment"
          }), "\n                      ", HTML.SPAN({
            class: "badge-numFav badge"
          }, Blaze.View("lookup:numComment", function() {
            return Spacebars.mustache(view.lookup("numComment"));
          })), "\n                    "), "\n                  " ];
        }, function() {
          return [ "\n                    ", HTML.A({
            id: "btnComm",
            class: "btn btn-secondary"
          }, "\n                    ", HTML.SPAN({
            class: "glyphicon glyphicon-comment"
          }), "\n                  "), "\n                  " ];
        }), "\n                  ", Blaze.If(function() {
          return Spacebars.call(view.lookup("numFavorite"));
        }, function() {
          return [ "\n                    ", HTML.A({
            id: "btnFav",
            class: "btn btn-secondary"
          }, "\n                      ", HTML.SPAN({
            id: function() {
              return Spacebars.mustache(view.lookup("idToFavBtn"));
            },
            class: function() {
              return [ "glyphicon glyphicon-heart ", Spacebars.mustache(view.lookup("classFav")) ];
            }
          }), "\n                      ", HTML.SPAN({
            class: "badge badge-numFav"
          }, Blaze.View("lookup:numFav", function() {
            return Spacebars.mustache(view.lookup("numFav"));
          })), "\n                    "), "\n                  " ];
        }, function() {
          return [ "\n                    ", HTML.A({
            id: "btnFav",
            class: "btn btn-secondary"
          }, "\n                      ", HTML.SPAN({
            id: function() {
              return Spacebars.mustache(view.lookup("idToFavBtn"));
            },
            class: function() {
              return [ "glyphicon glyphicon-heart ", Spacebars.mustache(view.lookup("classFav")) ];
            }
          }), "\n                    "), "\n                  " ];
        }), "\n                "), "\n              "), "\n            " ];
      }), "\n             ", HTML.BUTTON({
        id: "btnDismissNotif",
        class: "btn btn-info pull-right",
        type: "button"
      }, "OK"), "\n          " ];
    }, function() {
      return [ "\n            ", HTML.DIV({
        class: "panel-info"
      }, "\n              ", HTML.P("Oooooooops Twiit Page New"), "    \n            "), "\n          " ];
    }), "\n        "), "\n      "), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("loading")), "\n  " ];
  }) ];
}));
