
Template.__checkName("notificationsNew");
Template["notificationsNew"] = new Template("Template.notificationsNew", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n    ", HTML.A({
      id: "linkToNotif",
      class: "dropdown-toggle",
      "data-toggle": "dropdown",
      role: "button",
      "aria-haspopup": "true",
      "aria-expanded": "false"
    }, "\n    ", HTML.Comment("Notificaciones"), "\n    ", HTML.SPAN({
      class: "glyphicon glyphicon-inbox"
    }), "\n\n      ", Blaze.If(function() {
      return Spacebars.call(view.lookup("notificationCount"));
    }, function() {
      return [ "\n        ", Blaze.If(function() {
        return Spacebars.call(view.lookup("modeDisplay"));
      }, function() {
        return [ "\n          ", HTML.SPAN({
          class: "badge badge-inverse"
        }, Blaze.View("lookup:notificationCount", function() {
          return Spacebars.mustache(view.lookup("notificationCount"));
        })), "\n        " ];
      }, function() {
        return [ "\n          ", Blaze.If(function() {
          return Spacebars.call(view.lookup("infoStateCollapseNavBar"));
        }, function() {
          return [ "\n            ", HTML.SPAN({
            class: "badge badge-inverse"
          }, Blaze.View("lookup:notificationCount", function() {
            return Spacebars.mustache(view.lookup("notificationCount"));
          })), "\n          " ];
        }), "\n        " ];
      }), "\n      " ];
    }), "\n\n    "), "\n    ", Blaze.Unless(function() {
      return Spacebars.call(view.lookup("notificationCount"));
    }, function() {
      return [ "\n    ", HTML.UL({
        class: "notification dropdown-menu dropdown-menu-center"
      }, "\n      ", HTML.LI(HTML.SPAN("No Notifications")), "\n    "), "\n    " ];
    }), "\n  " ];
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("loading")), "\n  " ];
  });
}));
