
Template.__checkName("tweetFeed");
Template["tweetFeed"] = new Template("Template.tweetFeed", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n    ", Blaze.View("lookup:infoNotif", function() {
      return Spacebars.mustache(view.lookup("infoNotif"));
    }), "\n    ", HTML.DIV({
      class: "tweetfeed-container"
    }, "\n      ", HTML.DIV({
      class: "panel panel-default tweetfeed"
    }, "\n        ", HTML.DIV({
      class: "panel-body"
    }, "\n          ", HTML.Comment(" Texto para el contenido del Twitt "), "\n          ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("tweetMessage"));
    }, function() {
      return [ "\n            ", HTML.DIV({
        class: "panel panel-info"
      }, HTML.Comment("Contenido de la cabecera + el mensaje del twiit"), "\n              ", HTML.DIV({
        class: "panel-heading"
      }, HTML.Comment("Cabecera del twiit"), "\n                ", HTML.H3({
        class: "panel-title"
      }, "\n                  ", HTML.A({
        href: function() {
          return Spacebars.mustache(view.lookup("pathFor"), "userProfile", Spacebars.kw({
            username: Spacebars.dot(view.lookup("."), "user")
          }));
        }
      }, "@", Blaze.View("lookup:..user", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "user"));
      })), " \n                  ", HTML.SPAN({
        class: "glyphicon glyphicon-triangle-right",
        "aria-hidden": "true"
      }), "\n                  ", Blaze.View("lookup:convertDateTime", function() {
        return Spacebars.mustache(view.lookup("convertDateTime"));
      }), "\n                "), "\n              "), HTML.Comment("Cabecera del twiit"), "\n              ", HTML.DIV({
        class: "panel-body"
      }, HTML.Comment("Contenido del twiit"), "\n                ", Blaze.View("lookup:..message", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "message"));
      }), "\n              "), HTML.Comment("Contenido del twiit"), "\n\n              ", HTML.DIV({
        class: "btn-group",
        role: "group",
        id: "btnGroupInteractions"
      }, "\n                ", HTML.A({
        class: "btn btn-secondary"
      }, "\n                  ", HTML.SPAN({
        class: "glyphicon glyphicon-bullhorn"
      }), "\n                "), "\n                ", Blaze.If(function() {
        return Spacebars.call(view.lookup("numComment"));
      }, function() {
        return [ "\n                  ", HTML.A({
          href: function() {
            return Spacebars.mustache(view.lookup("pathFor"), "twiitCommentPage", Spacebars.kw({
              _id: Spacebars.dot(view.lookup("."), "_id")
            }));
          },
          class: "btn"
        }, "\n                    ", HTML.SPAN({
          class: "glyphicon glyphicon-comment"
        }), "\n                    ", HTML.SPAN({
          class: "badge badge-numFav "
        }, Blaze.View("lookup:..numComment", function() {
          return Spacebars.mustache(Spacebars.dot(view.lookup("."), "numComment"));
        })), "\n                  "), "\n                " ];
      }, function() {
        return [ "\n                  ", HTML.A({
          id: "btnComm",
          class: "btn btn-secondary"
        }, "\n                    ", HTML.SPAN({
          class: "glyphicon glyphicon-comment"
        }), "\n                "), "\n                " ];
      }), "\n                ", Blaze.If(function() {
        return Spacebars.call(view.lookup("numFav"));
      }, function() {
        return [ "\n                ", HTML.A({
          id: "btnFav",
          class: "btn btn-secondary"
        }, "\n                  ", HTML.SPAN({
          id: function() {
            return Spacebars.mustache(view.lookup("idToFavBtn"));
          },
          class: function() {
            return [ "glyphicon glyphicon-heart ", Spacebars.mustache(view.lookup("classFav")) ];
          }
        }), "\n                  ", HTML.SPAN({
          class: "badge badge-numFav"
        }, Blaze.View("lookup:..numFav", function() {
          return Spacebars.mustache(Spacebars.dot(view.lookup("."), "numFav"));
        })), "\n                "), "\n                " ];
      }, function() {
        return [ "\n                  ", HTML.A({
          id: "btnFav",
          class: "btn btn-secondary"
        }, "\n                  ", HTML.SPAN({
          id: function() {
            return Spacebars.mustache(view.lookup("idToFavBtn"));
          },
          class: function() {
            return [ "glyphicon glyphicon-heart ", Spacebars.mustache(view.lookup("classFav")) ];
          }
        }), "\n                "), "\n                " ];
      }), "\n              "), "\n            "), HTML.Comment("Contenido de la cabecera + el mensaje del twiit"), "\n          " ];
    }), "\n        "), "\n      "), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("loading")), "\n  " ];
  });
}));
